import React from 'react'
import { Layout, Breadcrumb, Row, Col, Divider } from 'antd'
import { HomeOutlined } from '@ant-design/icons'
import { Tree } from 'antd';
import CodeDetail from './CodeDetail';

const { DirectoryTree } = Tree;

const treeData = [
    {
        title: 'Java公开库',
        key: '0-0',
        children: [
            {
                title: '工具',
                key: '0-aac',
                isLeaf: false,
                children: [
                    {
                        title: '加密',
                        key: '0-12-1-1',
                        isLeaf: true
                    }
                ]
            },
            {
                title: '集成',
                key: '0-aae',
                isLeaf: false,
                children: [
                    {
                        title: '快速部署工具',
                        key: '0-12-1-12',
                        isLeaf: true
                    }
                ]
            },

        ],
    },
    {
        title: 'Python公开库',
        key: 'pypub',
        children: [
            {
                title: '加密模块',
                key: '0-1-0',
                isLeaf: false,
                children: [
                    { title: 'MD5Util', key: '0-1-1-1', isLeaf: true }
                ]
            },
            {
                title: '工具模块',
                key: '0-1233',
                isLeaf: false,
                children: [
                    { title: '语音合成工具', key: '1-22-1-1', isLeaf: true }
                ]
            },
        ],
    },
];

const { Content } = Layout

export default class CodeLibrary extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            detailId: -1
        }
    }
    onSelect = (selectedKeys, e) => {
        //处理列表项选择
        if (e.selectedNodes[0].isLeaf) {
            this.setState({
                detailId: selectedKeys[0]
            })
        }
    }
    onExpand = (selectedKeys, e) => {
        //处理列表展开
    }
    render() {
        return (
            <Content style={{ margin: '0 16px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item><HomeOutlined /></Breadcrumb.Item>
                    <Breadcrumb.Item>代码库</Breadcrumb.Item>
                    <Breadcrumb.Item>公开库</Breadcrumb.Item>
                </Breadcrumb>

                <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                    <h1>公开库查询</h1>
                    <Divider />
                    <Row>
                        <Col xs={24} md={24} lg={8}>
                            <DirectoryTree
                                defaultExpandAll
                                onSelect={this.onSelect}
                                onExpand={this.onExpand}
                                treeData={treeData}
                            />

                        </Col>
                        <Col xs={24} lg={16}>
                            <CodeDetail id={this.state.detailId} />
                        </Col>
                    </Row>
                </div>
            </Content>
        )
    }
}