import React from 'react'
import { Layout, Descriptions, Divider, Typography, Empty } from 'antd';



export default class CodeDetail extends React.Component {
    render() {
        return (
            <>
                <Empty
                    style={{ marginTop: '10vh' }}
                    hidden={this.props.id !== -1 ? true : false}
                    image={Empty.PRESENTED_IMAGE_SIMPLE}
                    description="请选择模块"
                />
                <Layout.Content
                    style={{ margin: '16px' }}
                    hidden={this.props.id === -1 ? true : false}
                >
                    <Descriptions
                        title="XXXX加密模块"
                        size="small"
                    >
                        <Descriptions.Item label="作者"><Typography.Link>@ThCode</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="日期">2021/02/21</Descriptions.Item>
                        <Descriptions.Item label="版本">1.0.0</Descriptions.Item>
                        <Descriptions.Item label="引用"><Typography.Link>MD5Encrypt</Typography.Link>、<Typography.Link>RSAUtil</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="转载"><Typography.Link>首次上传</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="编号"><Typography.Link>{this.props.id}</Typography.Link></Descriptions.Item>

                    </Descriptions>
                    <Divider />
                    <Descriptions
                        bordered
                        size="small"
                        column={1}
                    >
                        <Descriptions.Item label="使用说明">
                            导入xxx模块：<Typography.Link>模块xxx</Typography.Link><br />
                        安装方法：……
                    </Descriptions.Item>

                        <Descriptions.Item label="前置导入">
                            import java.xxxxxx<br />
                        import java.yyyyyyyyy<br />
                        import java.zzzzzzzzzzzzzz
                    </Descriptions.Item>

                        <Descriptions.Item label="代码体">
                            {"public class Test{"}<br />
                        &nbsp;&nbsp;&nbsp;&nbsp;......<br />
                            {"}"}
                            <br />
                        </Descriptions.Item>
                    </Descriptions>
                </Layout.Content>
            </>
        )
    }
}