import React from 'react'
import { Layout, Breadcrumb, Row, Col, Timeline, Card, Divider, Badge } from 'antd'
import { HomeOutlined } from '@ant-design/icons'
import LogData from './changelog.json'

const { Content } = Layout
export default class ChangeLog extends React.Component {
    render() {
        return (
            <Content style={{ margin: '0 16px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item><HomeOutlined /></Breadcrumb.Item>
                    <Breadcrumb.Item>公共查询</Breadcrumb.Item>
                    <Breadcrumb.Item>更新日志</Breadcrumb.Item>
                </Breadcrumb>
                <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                    <Row>
                        <Col xs={0} sm={8} />
                        <Col xs={24} lg={8}>
                            <h1>Change log</h1>
                            <Divider />
                            <Timeline>
                                {LogData.data.map((item) => {
                                    return (
                                        <Timeline.Item key={item.key} color={item.special !== "" ? item.special : "gray"}>
                                            <Card>
                                                <p>{item.version}</p>
                                                <p>
                                                    {item.items.map((item, index) => {
                                                        return (<span key={index}>{item}<br /></span>)
                                                    })}
                                                </p>
                                                <Badge color={item.tags.indexOf("func") === -1 ? "gray" : "blue"} text="功能更新" />
                                                <Divider type="vertical" />
                                                <Badge color={item.tags.indexOf("view") === -1 ? "gray" : "green"} text="界面更新" />
                                                <Divider type="vertical" />
                                                <Badge color={item.tags.indexOf("bugfix") === -1 ? "gray" : "red"} text="问题修复" />
                                            </Card>
                                        </Timeline.Item>
                                    )
                                })}
                            </Timeline>
                        </Col>
                    </Row>
                </div>
            </Content>
        )
    }
}